/* styles.css */

.createMoviePage,
.editMoviePage {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    min-height: 100vh;
    background-color: #f8f9fa;
    padding: 20px;
    box-sizing: border-box;
}

.formContainer {
    background: #ffffff;
    padding: 30px;
    width: 100%;
    max-width: 500px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    display: flex;
    flex-direction: column;
    gap: 15px;
    margin-top: 20px;
    margin-bottom: 20px;
}

.formContainer label {
    font-weight: bold;
    color: #333333;
    margin-bottom: 5px;
}

#inputCreateMovie,
#inputEditMovie {
    padding: 8px;
    width: 100%;
    border: 1px solid #cccccc;
    border-radius: 4px;
    font-size: 16px;
    box-sizing: border-box;
}

#inputCreateMovie[type="file"],
#inputEditMovie[type="file"] {
    padding: 0;
}

button[type="submit"] {
    background-color: #007bff;
    color: #ffffff;
    padding: 10px;
    font-size: 16px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

button[type="submit"]:hover {
    background-color: #0056b3;
}

span {
    color: #ff4d4d;
    font-size: 14px;
    margin-top: -5px;
    margin-bottom: 5px;
}

img {
    display: block;
    margin-top: 10px;
    max-width: 100%;
    height: auto;
    border: 1px solid #dddddd;
    border-radius: 4px;
}


.descriptionTextarea {

    padding: 8px;
    width: 100%;
    border: 1px solid #cccccc;
    border-radius: 4px;
    font-size: 16px;
    box-sizing: border-box;
    resize: none;
}

.alert {
    text-align: left;
    color: red;
}

/* MovieDetails.css */

.seat-selection {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    /* Align items to the start */
    width: 100%;
}

.seat-toggle {
    background-color: #007bff;
    /* Blue color */
    color: white;
    border: none;
    padding: 10px 12px;
    /* Adjust padding for a better button size */
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.2s ease;
    margin: 0 5%;
}

.confirm-button {
    background-color: #4caf50;
    /* Green color */
    color: white;
    border: none;
    padding: 10px 12px;
    /* Adjust padding for a better button size */
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.2s ease;
    margin-left: 5%;
    /* Add transition effect */
}

.seat-toggle:hover {
    background-color: #0056b3;
    /* Darker blue on hover */
}

.confirm-button:hover {
    background-color: #388e3c;
    /* Darker green on hover */
}

.seat-count {
    margin: 0 10px;
    font-size: 18px;
    color: black;
}

.start-time-time {
    font-size: 1.5rem;
    /* Smaller font size */
    font-weight: bold;
    color: #2d3748;
    /* Darker text color */
    text-align: left;
    /* Align text to the left */
    margin-right: auto;
    /* Push other elements to the right */
}

/* SeatSelection.css */

.seat-selection-page {
    padding: 20px;
    text-align: center;
}

.seat-map {
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;
    margin: 20px 0;
}

.seat-row {
    display: flex;
    gap: 5px;
    justify-content: center;
}

.seat {
    width: 40px;
    height: 40px;
    background-color: #d3d3d3;
    border-radius: 4px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    color: #333;
    transition: background-color 0.3s;
}

.seat.selected {
    background-color: #4caf50;
    /* Green for selected seats */
    color: white;
}

.seat.occupied {
    background-color: #e57373;
    /* Red for occupied seats */
    cursor: not-allowed;
}

.confirm-selection-button {
    padding: 10px 20px;
    font-size: 16px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.confirm-selection-button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}

/* Seatmap */
.seatMap {
    background-color: #242333;
    display: flex;
    flex-direction: column;
    font-family: 'Lato', sans-serif;
    color: white;
    align-items: center;
    /* Center items horizontally */
    justify-content: center;
    /* Center items vertically */
    height: 100vh;
}

.seat {
    background-color: #444451;
    height: 22px;
    width: 25px;
    margin: 3px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.row {
    display: flex;
    justify-content: center;
}

.seat.selected {
    background-color: #e97c00;
}

.seat.occupied {
    background-color: #e0e0e0;
}

.seat:nth-of-type(2) {
    margin-right: 28px;
}

.seat:nth-last-of-type(2) {
    margin-left: 28px;
}

.seat:not(.occupied):hover {
    cursor: pointer;
    transform: scale(1.2);
}

.showcase .seat:not(.occupied):hover {
    cursor: default;
    transform: scale(1);
}

.showcase {
    background-color: rgba(0, 0, 0, 0.1);
    padding: 5px 10px;
    border-radius: 5px;
    display: flex;
    list-style-type: none;
    justify-content: space-between;
    color: #777;
}

.showcase li {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 10px;
}

.showcase li small {
    margin-left: 10px;
}

.screen {
    background-color: #fff;
    color: black;
    /* Changed to black for better visibility */
    text-align: center;
    margin: 25px 0;
    /* Center vertically by margin */
    padding: 10px;
    font-size: 1.5rem;
    height: 70px;
    width: 30%;
    /* Adjusted to a suitable width for centering */
    max-width: 600px;
    /* Optional: Max width to prevent it from being too wide */
    transform: rotateX(-45deg);
    box-shadow: 0 3px 10px rgba(255, 255, 255, 0.75);
    display: flex;
    /* Added flexbox to center text inside */
    align-items: center;
    /* Center text vertically */
    justify-content: center;
    /* Center text horizontally */
}

.container {
    perspective: 1000px;
    display: flex;
    /* Use flexbox for alignment */
    flex-direction: column;
    /* Align children vertically */
    align-items: center;
    /* Center children horizontally */
    width: 100%;
    /* Full width for the container */
}

p.text {
    margin: 25px 0;
    font-size: 15pt;
}

p.text span {
    color: #e97c00;
}

.confirm-button {
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    transition: background-color 0.3s;
    margin-top: 20px;
    /* Space above the button */
    width: 50%;
    /* Set to 50% to match screen width */
    max-width: 200px;
    /* Limit maximum width */
    display: block;
    /* Ensure it's treated as a block element */
    text-align: center;
    /* Center text inside the button */
    margin: 25px 0;
}

#count,
#total,
#current-count {
    font-size: 15pt;
}

.confirm-button:disabled {
    background-color: #ccc;
    /* Change to a gray color */
    cursor: not-allowed;
    /* Change cursor to indicate the button is disabled */
}